.container {
  width: 100%;
  height: 100vh;
  display: flex;
  background-color: cadetblue;
}

.square {
  width: 120px;
  height: 120px;
  background-color: #fff;
  margin: 10px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  position: relative;
  font-weight: bolder;
}

.board {
  margin-right: 32px;
}

.button {
  width: 200px;
  height: 32px;
  background-color: #fff;
  font-size: 24px;
  display: block;
}
.row {
  display: flex;
}

.button:hover {
  background-color: #000;
  color: #fff;
}

.button[data-active="true"] {
  background-color: olivedrab;
  color: #fff;
}
